<template>
  <button type="button" class="button" :class="cls" :style="{color: color}" v-if="isButton">
    <span class="icon" :class="{alone: !showName}">
      <i :class="['fa', iconName]"></i>
    </span>
    <span v-if="showName"><slot></slot></span>
  </button>
  <a href="javascript:void(0)" :class="cls" :style="{color: color}" v-else>
    <span class="icon">
      <i :class="['fa', iconName]"></i>
    </span>
    <span v-if="showName"><slot></slot></span>
  </a>
</template>

<script>
export default {
  name: 'btn',
  props: {
    icon: String,
    cls: [Array, String, Object],
    color: String,
    type: {
      type: String,
      default: 'button'
    }
  },
  computed: {
    iconName () {
      return 'fa-' + this.icon
    },
    showName () {
      return !!this.$slots.default && !!this.$slots.default[0]
    },
    isButton () {
      return this.type === 'button'
    }
  }
}
</script>

<style lang="scss" scoped>
.icon {
  &.alone {
    margin: 0;
  }
}
</style>



