import path from 'path'
import forIn from 'lodash.forin'
import forEach from 'lodash.foreach'
import has from 'lodash.has'
import includes from 'lodash.includes'
import mimeType from 'mime-types'
import filesize from 'file-size'

const TYPES = {
  image: [
    'jpg', 'png', 'jpeg', 'gif'
  ],
  archive: [
    'rar', 'zip', '7z', 'tar', 'gz',
    'gzip', 'tgz', 'bz2', 'bzip2'
  ],
  docs: [
    'doc', 'docm', 'dotx', 'dotm', 'docb',
    'dot', 'wbk'
  ],
  excel: [
    'xls', 'xlt', 'xlm', 'xlsx', 'xlsm', 'xltx',
    'xltm', 'xlsb', 'xls', 'xlam', 'xll', 'xlw'
  ],
  powerpoint: [
    'ppt', 'pot', 'pps', 'pptx', 'pptm',
    'potx', 'potm', 'ppam', 'ppsx',
    'ppsm', 'sldx', 'sldm'
  ]
}

const convertTypes = () => {
  const ret = {}

  forIn(TYPES, (value, key) => {
    forEach(value, v => (ret[v] = key))
  })

  return ret
}

export default {
  methods: {
    file ({filename, size, mime, type, dir, status, url, thumbnail_url}) { //eslint-disable-line
      if (!filename) {
        return {}
      }

      const isYoutube = () => includes(filename, 'youtu')
      const typeList = convertTypes()
      const getExt = () => path.extname(filename)
      const getSize = () => filesize(size).human()
      const getType = () => {
        if (type) {
          return type
        } else if (isYoutube()) {
          return 'youtube'
        }

        let ext = getExt().slice(1)

        if (!ext || (ext && !has(typeList, ext))) {
          ext = mimeType.extension(mime)
        }

        return has(typeList, ext) ? typeList[ext] : getExt()
      }
      const getThumbnail = () => {
        if (typeof thumbnail_url === 'string') { //eslint-disable-line
          return thumbnail_url // eslint-disable-line
        }

        return url
      }

      return {
        filename,
        ext: getExt(),
        type: getType(),
        size: getSize(),
        dir,
        status,
        url,
        thumbnail_url: getThumbnail() // eslint-disable-line
      }
    }
  }
}



