import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
  selected: {
    uid: -1,
    filename: '',
    type: false
  },
  contextMenuShowed: false,
  previewing: false,
  renaming: false,
  loading: false,
  uploading: {}
}

const mutations = {
  FILE_SELECT (state, value) {
    state.selected = {
      uid: -1,
      filename: '',
      type: false
    }
    state.selected = value
  },
  FILE_UNSELECT (state) {
    state.selected = {
      uid: -1,
      filename: '',
      type: false
    }
  },
  CONTEXT_MENU_SHOW (state) {
    state.contextMenuShowed = true
  },
  CONTEXT_MENU_HIDE (state) {
    state.contextMenuShowed = false
  },
  ACTION_PREVIEW_SHOW (state) {
    state.previewing = true
  },
  ACTION_PREVIEW_HIDE (state) {
    state.previewing = false
  },
  ACTION_RENAMING_SHOW (state) {
    state.renaming = true
  },
  ACTION_RENAMING_HIDE (state) {
    state.renaming = false
  },
  ACTION_DOWNLOAD (state, id) {
    state.downloading = id
  },
  LOADING_SHOW (state) {
    state.loading = true
  },
  LOADING_HIDE (state) {
    state.loading = false
  },
  IS_UPLOADING (state, fileDef) {
    state.uploading = fileDef
  }
}

const actions = {
  CONTEXT_MENU_SHOW: ({ commit }) => commit('CONTEXT_MENU_SHOW'),
  CONTEXT_MENU_HIDE: ({ commit }) => commit('CONTEXT_MENU_HIDE'),
  CONTEXT_MENU_RESHOW ({ commit }) {
    this.CONTEXT_MENU_SHOW(commit)
    setTimeout(() => this.CONTEXT_MENU_HIDE(commit), 10)
  }
}

const getters = {
  selected: state => state.selected,
  contextMenuShowed: state => state.contextMenuShowed,
  previewing: state => state.previewing,
  renaming: state => state.renaming,
  loading: state => state.loading,
  uploading: state => state.uploading
}

// A Vuex instance is created by combining the state, mutations, actions,
// and getters.
export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



