<template>
  <div class="nav-fm">
    <div class="nav-fm-center is-half">
      <div class="nav-fm-item">
        <input type="text" :value="filename" class="input" @keypress.enter="submitNewName">
      </div>
      <div class="cancel" @click="cancelRename">
        <btn type="a" icon="remove" cls="cancel-btn"></btn>
      </div>
    </div>
</template>

<script>
import Btn from './partials/Btn'
import {mapMutations, mapGetters} from 'vuex'

export default {
  name: 'rename-file',
  components: { Btn },
  methods: {
    ...mapMutations([
      'ACTION_RENAMING_SHOW',
      'ACTION_RENAMING_HIDE'
    ]),
    cancelRename () {
      this.ACTION_RENAMING_HIDE()
    },
    submitNewName () {
      this.ACTION_RENAMING_HIDE()
    }
  },
  computed: {
    ...mapGetters([
      'selected',
      'renaming'
    ]),
    filename () {
      return this.selected && this.selected.name
    }
  }
}
</script>

<style lang="scss" scoped>
.nav-fm {
  position: relative;
}
.nav-fm-center {
  width: 100%;

  .nav-fm-item {
    padding: 0;
    width: 100%;
  }
}

@media (min-width: 780px) {
  .nav-fm-center {
    width: 50%;
  }
}
.input {
  height: 100%;
  box-shadow: none;
  border-radius: 0;
  font-size: 140%;
}
.cancel {
  position: absolute;
  right: 0;
  height: 100%;
  display: flex;
  padding: 10px;
  justify-content: center;
  align-items: center;

  .cancel-btn {
    color: #fff;

    &:hover {
      color: darken(#fff, 7%);
      transition: .2s ease color;
    }
  }
}
</style>



