<template>
  <div v-if="previewing">
    <modal :show="previewing" @modalclose="closePreview">
      <img v-lazy="src" v-if="isType('image')">
      <iframe
        frameborder="0" scrolling="no" marginheight="0" marginwidth="0"
        width="788.54" height="443" type="text/html"
        :src="src"
        v-else-if="isType('youtube')"
      >
      </iframe>
    </modal>
  </div>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex'
import Modal from './Modal'
import {getId} from '../../utils/youtube'

export default {
  name: 'preview-modal',
  components: { Modal },
  data () {
    return {
      showPreview: false
    }
  },
  methods: {
    ...mapMutations([
      'ACTION_PREVIEW_HIDE'
    ]),
    closePreview () {
      this.ACTION_PREVIEW_HIDE()
      return (this.showPreview = false)
    },
    isType (type) {
      return this.selected && this.selected.type === type
    }
  },
  computed: {
    ...mapGetters([
      'selected',
      'previewing'
    ]),
    showing () {
      return (this.showPreview = this.previewing)
    },
    src () {
      if (this.isType('image')) {
        return this.selected.thumbnail || this.selected.url
      } else if (this.isType('youtube')) {
        if (!this.selected.filename) {
          return
        }

        return 'https://www.youtube.com/embed/' + getId(this.selected.filename) +
        '?autoplay=0&fs=0&iv_load_policy=3&showinfo=0&rel=0&cc_load_policy=0&start=0&end=0'
      }
    }
  }
}
</script>



