<template>
  <div class="hero-head">
    <div class="nav-fm">
      <div class="nav-fm-left">
        <div class="nav-fm-item">
          <div class="tabs" v-show="hasSelected">
            <ul>
              <li v-for="(value, key) in file" :key="key">
                {{ key }}: {{ value }}
              </li>
            </ul>
          </div>
        </div>
      </div>

      <div class="nav-fm-right nav-fm-menu">
        <div class="is-tab nav-fm-item">
          <span class="tag" v-show="hasSelected">
            {{ type }}
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import pick from 'lodash.pick'

const DEFAULT_FILE = {
  name: '',
  size: '',
  type: ''
}

export default {
  name: 'file-info',
  computed: {
    type () {
      return this.selected.type
    },
    hasSelected () {
      if (!this.selected.type) {
        return false
      }

      return this.selected.type.length > 0
    },
    ...mapGetters([
      'selected'
    ]),
    file () {
      if (this.selected.type) {
        return pick(this.selected, ['filename', 'size', 'mime'])
      }

      return DEFAULT_FILE
    }
  }
}
</script>

<style lang="scss" scoped>
$family-monospace: 'Inconsolata', 'Consolas', 'Monaco', monospace !default;

.nav-fm-left {
  overflow: hidden;
  flex-basis: 75%;
}
.nav-fm-right {
  width: 20%;
}
.tabs {
  li {
    margin-left: 2vw;
    font-size: 80%;
    color: lighten(#333, 90%);
    font-family: $family-monospace;

    &:first-child {
      margin-left: 0;
    }
  }
}

</style>



