<template>
  <div v-if="showing">
    <modal :show="showing" @modalclose="closePreview">
      <input type="text" placeholder="Youtube URL">
    </modal>
  </div>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex'
import Modal from './Modal'

export default {
  name: 'upload-modal',
  components: { Modal },
  data () {
    return {
      showPreview: false
    }
  },
  methods: {
    ...mapMutations([
      'UPLOADING'
    ])
  },
  computed: {
    ...mapGetters([
      'uploading'
    ]),
    showing () {
      const show = typeof this.uploading === 'object' &&
          typeof this.uploading.type === 'string' &&
          this.uploading.type === 'youtube'

      console.log(show, this.uploading)
    }
  }
}
</script>



