<template>
  <div id="app">
    <navigation></navigation>
    <info-bar></info-bar>
    <manager></manager>
  </div>
</template>

<script>
import {mapMutations} from 'vuex'
import Navigation from './components/Navigation'
import InfoBar from './components/InfoBar'
import Manager from './components/Manager'

export default {
  name: 'app',
  components: {
    Navigation, InfoBar, Manager
  },
  beforeMounted () {
    this.LOADING_SHOW()
  },
  mounted () {
    this.LOADING_HIDE()
  },
  methods: {
    ...mapMutations([
      'LOADING_SHOW',
      'LOADING_HIDE'
    ])
  }
}
</script>

<style lang="scss">
$family-sans-serif: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif !default;
$family-monospace: 'Inconsolata', 'Consolas', 'Monaco', monospace !default;
$blue: #3c8dbc;
$primary: $blue;
$link: #7a7a7a;

@import "~bulma/sass/utilities/_all";
@import "~bulma/sass/base/_all";
@import "~bulma/sass/elements/_all";
@import "~bulma/sass/components/card.sass";
@import "~bulma/sass/components/highlight.sass";
@import "~bulma/sass/components/level.sass";
@import "~bulma/sass/components/media.sass";
@import "~bulma/sass/components/menu.sass";
@import "~bulma/sass/components/message.sass";
@import "~bulma/sass/components/modal.sass";

#app {
  font-family: $family-sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: floralwhite;
  color: #333;
}

$nav-height: 50px !default;

.nav-fm-item {
  align-items: center;
  display: flex;
  flex-grow: 0;
  flex-shrink: 0;
  justify-content: center;
  padding: 10px;
  a {
    flex-grow: 1;
    flex-shrink: 0
  }
  img {
    max-height: 24px;
  }
  .button + .button {
    margin-left: 10px;
  }
  .tag {
    &:first-child {
      margin-right: 5px;
    }
    &:last-child {
      margin-left: 5px;
    }
  }
  // Responsiveness
  @include mobile {
    justify-content: flex-start;
  }
}

.nav-fm-item a,
a.nav-fm-item {
  color: $text-light;
  &:hover {
    color: $link-hover;
  }
  // Modifiers
  &.is-active {
    color: $link-active;
  }
  &.is-tab {
    border-bottom: 1px solid transparent;
    border-top: 1px solid transparent;
    padding-left: 12px;
    padding-right: 12px;
    &:hover {
      border-bottom: 1px solid $link;
      border-top: 1px solid transparent;
    }
    &.is-active {
      border-bottom: 3px solid $link;
      border-top: 3px solid transparent;
      color: $link;
    }
  }
}
// Containers

.nav-fm-menu {
  // Responsiveness
  @include mobile {
    background-color: $white;
    box-shadow: 0 4px 7px rgba($black, 0.1);
    left: 0;
    display: none;
    right: 0;
    top: 100%;
    position: absolute;
    .nav-fm-item {
      border-top: 1px solid rgba($border, 0.5);
      padding: 10px;
    }
    &.is-active {
      display: block;
    }
  }
  @include tablet-only {
    padding-right: 20px;
  }
}

.nav-fm-left {
  align-items: stretch;
  display: flex;
  flex-basis: 0;
  flex-grow: 1;
  flex-shrink: 0;
  justify-content: flex-start;
  overflow: hidden;
  overflow-x: auto;
  white-space: nowrap;
}

.nav-fm-center {
  align-items: stretch;
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.nav-fm-right {
  // Responsiveness
  @include tablet {
    align-items: stretch;
    display: flex;
    flex-basis: 0;
    flex-grow: 1;
    flex-shrink: 0;
    justify-content: flex-end;
  }
}
// Main container

.nav-fm {
  align-items: stretch;
  background-color: transparent;
  display: flex;
  min-height: $nav-height;
  position: relative;
  text-align: center;
  z-index: 2;
  & > .container {
    align-items: stretch;
    display: flex;
    min-height: $nav-height;
    width: 100%;
    & > .nav-fm-left {
      & > .nav-fm-item:first-child:not(.is-tab) {
        padding-left: 0;
      }
    }
    & > .nav-fm-right {
      & > .nav-fm-item:last-child:not(.is-tab) {
        padding-right: 0;
      }
    }
  }
  .container > & {
    & > .nav-fm-left {
      & > .nav-fm-item:first-child:not(.is-tab) {
        padding-left: 0;
      }
    }
    & > .nav-fm-right {
      & > .nav-fm-item:last-child:not(.is-tab) {
        padding-right: 0;
      }
    }
  }
  // Modifiers
  &.has-shadow {
    box-shadow: 0 2px 3px rgba($black, 0.1);
  }
  // Responsiveness
  @include touch {
    & > .container,
    .container > & {
      & > .nav-fm-left {
        & > .nav-fm-item.is-brand:first-child {
          padding-left: 20px;
        }
      }
    }
  }
}

@import "~bulma/sass/components/pagination.sass";
@import "~bulma/sass/components/panel.sass";
@import "~bulma/sass/components/tabs.sass";
@import "~bulma/sass/grid/_all";
@import "~bulma/sass/layout/_all";

</style>



