<template>
  <div class="file column is-3 is-desktop">
    <div class="card" :class="{selected: isSelected}">
      <div class="card-image" @click="click">
        <div class="image">
          <img v-lazy="fileDef.thumbnail_url" alt="" v-if="hasThumbnail && (isType('image') || isType('youtube'))">
          <i class="fa fa-file-image-o fa-4x" v-else-if="!hasThumbnail && isType('image')"></i>
          <i class="fa fa-youtube fa-4x" v-else-if="!hasThumbnail && isType('youtube')"></i>
          <i class="fa fa-archive fa-4x" v-else-if="isType('archive')"></i>
          <i class="fa fa-file-word-o fa-4x" v-else-if="isType('docs')"></i>
          <i class="fa fa-file-excel-o fa-4x" v-else-if="isType('excel')"></i>
          <i class="fa fa-file-powerpoint-o fa-4x" v-else-if="isType('powerpoint')"></i>
          <i class="fa fa-folder fa-4x" v-else-if="isType('folder')"></i>
          <span class="tag is-info is-large" v-else-if="notDefinedFile">{{ fileDef.type && fileDef.type.slice(1) }}</span>
          <i class="fa fa-file-code-o fa-4x" v-else></i>
        </div>
      </div>

      <div class="card-footer filename">
        <p>{{ filename }}</p>
      </div>

      <transition name="slide">
      <div class="card-footer actions" v-show="isSelected">
        <div class="column" @click.prevent="actionDownload">
          <btn type="a" icon="download"></btn>
        </div>
        <div class="column" @click.prevent="actionPreview">
          <btn type="a" icon="eye"></btn>
        </div>
        <div class="column" @click.prevent="actionRenaming">
          <btn type="a" icon="pencil"></btn>
        </div>
        <div class="column">
          <btn type="a" icon="trash"></btn>
        </div>
      </div>
      </transition>
    </div>
  </div>
</template>

<script>
import Btn from './partials/Btn'
import {mapMutations, mapGetters} from 'vuex'
import FileMixin from '../mixins/file'
import assign from 'lodash.assign'
import startsWith from 'lodash.startswith'
import {limit} from '../utils/str'

export default {
  name: 'file',
  components: { Btn },
  mixins: [
    FileMixin
  ],
  props: {
    definition: {
      type: Object,
      default () {
        return {
          filename: '',
          path: '',
          size: '',
          mime: '',
          status: '',
          thumbnail: ''
        }
      }
    }
  },
  mounted () {
    this.fileDef = assign(
      this.definition,
      this.file(this.definition),
      {uid: this._uid}
    )
  },
  data () {
    return {
      showFooter: false,
      clicked: false,
      menu: false,
      cursorOffset: {},
      fileDef: {}
    }
  },
  methods: {
    ...mapMutations([
      'FILE_SELECT',
      'ACTION_PREVIEW_SHOW',
      'ACTION_PREVIEW_HIDE',
      'ACTION_RENAMING_SHOW',
      'ACTION_RENAMING_HIDE',
      'ACTION_DOWNLOAD'
    ]),
    footerShow (uid) {
      this.showFooter = true
    },
    footerHide () {
      this.showFooter = this.clicked
    },
    click () {
      this.ACTION_PREVIEW_HIDE()
      this.FILE_SELECT(this.fileDef)
    },
    isType (type) {
      return this.fileDef.type === type
    },
    actionPreview () {
      this.ACTION_PREVIEW_SHOW()
    },
    actionRenaming () {
      this.ACTION_RENAMING_SHOW()
    },
    actionDownload () {
      this.ACTION_DOWNLOAD(this.selected.id)
    }
  },
  computed: {
    ...mapGetters([
      'selected'
    ]),
    isSelected () {
      return this.selected.uid === this._uid
    },
    notDefinedFile () {
      return startsWith(this.fileDef.type, '.')
    },
    hasThumbnail () {
      return typeof this.fileDef.thumbnail_url === 'string' &&
          this.fileDef.thumbnail_url.length > 0
    },
    filename () {
      if (!this.fileDef.filename) {
        return this.fileDef.filename
      }

      return limit(this.fileDef.filename, 22)
    }
  }
}
</script>

<style lang="scss" scoped>
.file {
  position: relative;
  z-index: 3;

  .card.selected {
    border: .3rem solid #abc9e8;
  }
  .actions {
    position: absolute;
    bottom: 0;
    background-color: #524747;
    width: 100%;
    z-index: 4;

    .column {
      position: relative;
      z-index: 5;
      padding: 0;
      text-align: center;
      min-height: 2.443rem;
      vertical-align: middle;
      display: flex;
      justify-content: center;
      align-items: center;

      a, btn, button {
        z-index: 6;
        width: 100%;
        height: 100%;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
  .image {
    display: flex;
    min-height: 10rem;
    justify-content: center;
    align-items: center;

    > .fa {
      text-align: center;
      margin: 0 auto;
      width: 100%;
    }
  }
  .filename {
    border: none;
    background: transparent;
    padding: 1vw;
    overflow: hidden;
  }
}

.card-image {
  min-height: 175px;
}

@media (max-width: 979px) {
  .file {
    margin: 0 auto;
    width: 50% !important;
  }
}

@media (max-width: 767px) {
  .file {
    width: 80% !important;
  }
}

.fa-4x {
  font-size: 6rem;
}
img[lazy=loading], img[lazy=error] {
  max-height: 100px;
}
.slide-enter-active {
  transition: all .2s ease;
}
.slide-leave-active {
  transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-enter, .slide-leave-active {
  transform: translateX(10px);
  opacity: 0;
}
</style>



