<template>
  <div class="hero">
    <file-info v-show="!renaming"></file-info>
    <rename-file v-show="renaming"></rename-file>
  </div>
</template>

<script>
import FileInfo from './FileInfo'
import RenameFile from './RenameFile'
import {mapMutations, mapGetters} from 'vuex'

export default {
  name: 'info-bar',
  components: { FileInfo, RenameFile },
  methods: {
    ...mapMutations([
      'ACTION_RENAMING_SHOW',
      'ACTION_RENAMING_HIDE'
    ])
  },
  computed: {
    ...mapGetters([
      'selected',
      'renaming'
    ])
  }
}
</script>

<style lang="scss" scoped>
.hero {
  background-color: darken(#ffffff, 30%);
  box-shadow: inset -0.1rem -0.1rem 0.5rem #8a8a8a;
}
.slideInLeft {
  -webkit-animation-name: slideInLeft;
  animation-name: slideInLeft;
}
.slideInRight {
  -webkit-animation-name: slideInRight;
  animation-name: slideInRight;
}
.slide-rename-enter, .slide-rename-leave-active {
  opacity: 1;
}
.slide-info-enter, .slide-info-leave-active {
  opacity: 1;
}
.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
@keyframes slideInLeft {
  from {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
    visibility: visible;
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
@keyframes slideInRight {
  from {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    visibility: visible;
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
.fade-enter-active, .fade-leave-active {
  transition: all .2s ease;
}
.fade-enter, .fade-leave-active {
  transform: translateX(2px);
  opacity: 0;
}
</style>



