<template>
  <div>
    <div class="button div" @click="uploadFile">
      <btn ref="upload" cls="is-primary" icon="upload" @click>Upload</btn>
    </div>
    <div class="button div" @click="uploadYoutube">
      <btn cls="is-primary" icon="youtube-square">Youtube Video</btn>
    </div>
  </div>
</template>

<script>
import Btn from './partials/Btn'
import Modal from './partials/Modal'
import {mapGetters, mapMutations} from 'vuex'
import uploader from '../utils/uploader'

export default {
  name: 'upload',
  components: { Btn, Modal },
  mounted () {
    this.uploader = uploader()
    this.uploader.assignBrowse(this.$refs.upload.$el)
  },
  data () {
    return {
      uploader: false,
      youtubeModal: false
    }
  },
  methods: {
    ...mapMutations([
      'IS_UPLOADING'
    ]),
    showYoutubeUploader () {
      this.youtubeModal = true
    }
  },
  computed: {
    type () {
      return this.selected.type
    },
    hasSelected () {
      if (!this.selected.type) {
        return false
      }

      return this.selected.type.length > 0
    },
    ...mapGetters([
      'selected'
    ]),
    uploadFile () {
      return this.IS_UPLOADING({})
    },
    uploadYoutube () {
      return this.IS_UPLOADING({
        type: 'youtube'
      })
    }
  }
}
</script>

<style lang="scss" scoped>
$family-monospace: 'Inconsolata', 'Consolas', 'Monaco', monospace !default;

.tabs {
  li {
    margin-left: 2vw;
    font-size: 80%;
    color: lighten(#333, 90%);
    font-family: $family-monospace;

    &:first-child {
      margin-left: 0;
    }
  }
}
.div.button {
  border: 0;
  padding: 0;
}
</style>



