<template>
  <section class="hero is-medium is-light">
    <div class="hero-body is-flex">
      <div class="container">
        <div class="columns is-multiline is-desktop contain">
          <file v-for="def in files" :definition="def"></file>
        </div>
      </div>
    </div>
    <preview-modal></preview-modal>
    <upload-modal></upload-modal>
  </section>
</template>

<script>
import Btn from './partials/Btn'
import File from './File'
import PreviewModal from './partials/PreviewModal'
import UploadModal from './partials/UploadModal'
import {mapGetters, mapMutations} from 'vuex'
import ajax from '../utils/ajax'
import path from 'path'

export default {
  name: 'manager',
  components: { Btn, File, PreviewModal, UploadModal },
  data () {
    return {
      files: []
    }
  },
  mounted () {
    this.list()
  },
  methods: {
    ...mapMutations([
      'FILE_SELECT',
      'FILE_UNSELECT',
      'ACTION_DOWNLOAD'
    ]),
    list () {
      ajax('/')
        .then(res => (this.files = res.data))
        .catch(err => console.error('FILE MANAGER: ' + err))
    },
    setDownload () {
      if (!this.selected && !this.selected.id) {
        return
      }

      window.location = path.join(
        window.FileManager.url,
        'download',
        this.selected.id
      )
    },
    click () {
      this.FILE_UNSELECT()
    }
  },
  computed: {
    ...mapGetters([
      'downloading',
      'selected'
    ])
  }
}
</script>

<style lang="scss" scoped>
.hero, .contain {
  min-height: 250px;
}
.hero-body {
  padding: 3rem 1rem !important;
}
.contain {
  display: flex;
}
</style>



